<template>
  <div class="my">
    <div class="mybox">
      <div class="bg"></div>
      <div class="mybox-top">
        <div class="iconbox">
          <van-icon name="bullhorn-o" class="icon" color="#fff" size="20" />
        </div>
        <div class="headbox">
          <div class="headimg">
            <div class="imgbox" @click="goaccount">
              <img :src="isimgbool?userInfo.userImg:wdlImg" alt />
            </div>
            <div class="textbox" @click="goLogin">
              <span>{{isimgbool?userInfo.nickName:"请登录"}}</span>
            </div>
          </div>
          <div class="singin">
            <div class="singinicon"></div>
          </div>
        </div>
        <div class="cardbox">
          <div class="money card">
            <span>账户余额</span>
            <strong>70</strong>
          </div>
          <div class="preferential card">
            <span>优惠卷</span>
            <strong>7</strong>
          </div>
          <div class="gift card">
            <span>礼卡卷</span>
            <strong>1</strong>
          </div>
        </div>
      </div>
      <div class="mybox-body">
        <div class="listbox">
          <div
            class="listboxItem"
            v-for="(item,index) in listboxList"
            :key="index"
            @click="gothisname(item)"
          >
            <span>{{item.title}}</span>
            <van-icon name="arrow" size="20" color="#3f454b" />
          </div>
        </div>
      </div>
      <div class="mybox-foot"></div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/my.less";
export default {
  created() {
    this.getUserInfo();
  },
  data() {
    return {
      userInfo: [],
      isimgbool: false,
      wdlImg: require("../../assets/img/wdlhead.jpg"),
      listboxList: [
        {
          title: "个人详情",
          name: "Account",
        },
        {
          title: "我的订单",
          name: "Order",
        },
        {
          title: "我的收藏",
          name: "Mylive",
        },
        {
          title: "我的地址",
          name: "Manageaddress",
        },
        {
          title: "安全中心",
          name: "Security",
        },
      ],
    };
  },
  methods: {
    //点击头像
    goaccount() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$dialog
          .confirm({
            title: "请先登录",
          })
          .then(() => {
            this.$router.push("/login");
          })
          .catch(() => {
            this.$toast("取消登录");
          });
        return;
      }
      this.$router.push("/account");
    },
    //获取个人资料
    getUserInfo() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.isimgbool = false;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == "B001") {
            this.isimgbool = true;
            this.userInfo = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //跳转
    gothisname(item) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$dialog
          .confirm({
            title: "请先登录",
          })
          .then(() => {
            this.$router.push("/login");
          })
          .catch(() => {
            this.$toast("取消登录");
          });
        return;
      }

      this.$router.push({ name: item.name });
    },
    //请登录
    goLogin() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$router.push("/login");
      }
    },
  },
};
</script>

<style lang="less" scoped>
</style>